import React from 'react'
import { Button } from '../ButtonElement'
import { BtnWrap, Column1, Column2, Heading, Img, ImgWrap, InfoContainer, InfoRow, InfoWrapper, Subtitle, TextWrapper, TopLine } from './InfoElement'

const InfoSection = ({color, dark, dark2, lightBg, primary, id, imgStart, topLine, headline, lightText, darkText, descreption, buttonLabel, img, alt}) => {
    

    const headlineStyle = {
        color: `${color}` 
    }
    return (
        
        <>
            <InfoContainer lightBg={lightBg} id={id} >
                <InfoWrapper>
                    <InfoRow imgStart={imgStart} >
                        <Column1>
                            <TextWrapper>
                                <TopLine>{topLine}</TopLine>
                                <Heading style={headlineStyle} ligthText={lightText} >{headline}</Heading>
                                <Subtitle darkText={darkText} >{descreption}</Subtitle>
                                <BtnWrap>
                                    <Button to='home' smooth={true}
                                    duration={500}
                                    spy={true}
                                    exact='true'
                                    offset={-80}
                                    primary={primary ? 1 : 0 }
                                    dark={ dark ? 1 : 0 }
                                    dark2={dark2 ? 1 : 0}
                                    > {buttonLabel} </Button>
                                </BtnWrap>
                            </TextWrapper>
                        </Column1>
                        <Column2>
                            <ImgWrap>
                                <Img src={img} alt={alt} />
                            </ImgWrap>
                        </Column2>
                    </InfoRow>
                </InfoWrapper>
            </InfoContainer>
        </>
    )
}

export default InfoSection
